 */
/* Core CSS required for Ionic components to work properly */
@import "~@ionic/angular/css/core.css";
/* Basic CSS for apps built with Ionic */
@import "~@ionic/angular/css/normalize.css";
@import "~@ionic/angular/css/structure.css";
@import "~@ionic/angular/css/typography.css";
@import "~@ionic/angular/css/display.css";
/* Optional CSS utils that can be commented out */
@import "~@ionic/angular/css/padding.css";
@import "~@ionic/angular/css/float-elements.css";
@import "~@ionic/angular/css/text-alignment.css";
@import "~@ionic/angular/css/text-transformation.css";
@import "~@ionic/angular/css/flex-utils.css";
/** Padronização para animação de borda **/
.border-animated {
  background: var(--ion-holder-border);
  background-size: 200% 200%;
  animation: borderAnimation 4s ease-in-out infinite;
  width: 358px;
  height: 428px;
  margin: 0 auto !important;
}

@keyframes borderAnimation {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
/** Posicionamento de Toast (Ionic) **/
.toastAfterHeader .toast-wrapper {
  transform: translateY(100px) !important;
}

/** Personalização do tamanho dos Modais **/
.modal-size-80 {
  align-items: center;
  justify-content: center;
  --min-height: 80%;
  --min-width: 80%;
}

.modal-transparent {
  --ion-background-color: transparent;
  --backdrop-background-color: transparent;
}

.modal-size-100 {
  align-items: center;
  justify-content: center;
  --min-height: 100%;
  --min-width: 100%;
}

.modal-size-transacao-request .modal-wrapper {
  position: absolute;
  display: block;
  max-height: 370px;
  max-width: 80%;
}

/** Padronização de CARDs Ionic **/
.card-ipp {
  --color: white !important;
  text-align: center;
  align-items: center;
  justify-content: center;
  width: 350px;
  height: 420px;
  margin: 0 auto !important;
  --background: var(--ion-card-background);
}

.card-ipp:hover {
  cursor: pointer;
  background: rgba(65, 65, 65, 0.801);
}
.card-ipp:hover img {
  transform: scale(0.8);
}

.card-ipp img {
  max-width: 250px;
  max-height: 250px;
  transition: transform 0.2s;
  /* Animation */
  padding: 5px 10px;
  margin: auto;
}

.card-ipp ion-card-title {
  --color: white !important;
}

.card-ipp ion-card-content {
  --color: white !important;
  margin-top: 5px;
}

.card-ipp ion-card-title {
  font-weight: bold;
}

/** Padronização para Splitter **/
.card-splitter {
  --color: white !important;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  min-height: 245px;
  margin: 0 auto !important;
  --background: var(--ion-card-background);
  font-size: 2vh;
}

.card-splitter:hover {
  cursor: pointer;
  background: rgba(65, 65, 65, 0.801);
}
.card-splitter:hover img {
  transform: scale(0.8);
}

.card-splitter img {
  max-width: 500px !important;
  max-height: 500px !important;
  transition: transform 0.2s;
  /* Animation */
  padding: 5px 10px;
  margin: auto;
}

.card-splitter ion-card-title {
  --color: white !important;
  font-size: 3vh;
}

.card-splitter ion-card-content {
  --color: white !important;
  margin-top: 5px;
  font-size: 2.3vh;
}

.card-splitter ion-card-title {
  font-weight: bold;
}

.splitter-container {
  height: 90%;
  width: 90%;
  margin: 5px auto;
}

.background-icon {
  margin-top: 25px;
  font-size: 48px;
}

ion-title {
