}
.default-form p-inputmask {
  width: 100% !important;
}
.default-form .p-inputmask {
  width: 100% !important;
}
.default-form .p-inputtext {
  width: 100% !important;
}
.default-form p-password {
  max-height: 37px !important;
}
.default-form .p-password {
  width: 100% !important;
  max-height: 37px !important;
}
.default-form .btn-end {
