}

.main-view .data-container .center {
  text-align: center;
}
.main-view .data-container .p-dropdown {
  width: 14rem;
  font-weight: normal;
}
.main-view .data-container .data-name {
  font-size: 1.5rem;
  font-weight: 700;
}
.main-view .data-container .data-category-icon {
  vertical-align: middle;
  margin-right: 0.5rem;
}
.main-view .data-container .data-category {
  font-weight: 600;
  vertical-align: middle;
}
.main-view .data-container .data-list-item {
  display: flex;
  align-items: center;
  padding: 1rem;
  width: 100%;
}
.main-view .data-container .data-list-item .crop {
  height: 150px;
  overflow: hidden;
}
.main-view .data-container .data-list-item .crop img {
  width: 150px;
  margin: 0px 0px 0px 0px;
}
.main-view .data-container .data-list-item img {
  width: 150px;
  height: 150px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  margin-right: 2px;
}
.main-view .data-container .data-list-item .data-list-detail {
  margin-left: 10px;
  flex: 1 1 0;
}
.main-view .data-container .data-list-item .p-rating {
  margin: 0 0 0.5rem 0;
}
.main-view .data-container .data-list-item .data-price {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  align-self: flex-end;
}
.main-view .data-container .data-list-item .data-list-action {
  display: flex;
  flex-direction: column;
}
.main-view .data-container .data-list-item .p-button {
  margin-bottom: 0.5rem;
}

@media screen and (min-width: 576px) {
  .main-view .data-list-item img {
    margin-right: 2rem;
  }
}
@media screen and (max-width: 576px) {
  .main-view .data-list-item {
    flex-direction: column;
    align-items: center;
  }
  .main-view .data-list-item img {
    width: 75%;
    margin-right: 0;
    margin: 2rem 0;
  }
  .main-view .data-list-item .data-list-detail {
    text-align: center;
  }
  .main-view .data-list-item .data-price {
    align-self: center;
  }
  .main-view .data-list-item .data-list-action {
    display: flex;
    flex-direction: column;
  }
  .main-view .data-list-item .data-list-action {
    margin-top: 2rem;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}
